<template>
  <div class="content-wrapper">
    <div style="max-width: 1500px;margin: auto;">
      <div class="data-utils">
        <el-form inline class="search-form">
          <el-form-item>
            <el-input
              placeholder=""
              v-model="lazyParams.keyWord"
              class="input-with-select seedling_input"
            >
              <el-button slot="append" icon="el-icon-search" @click="searchKeyWord"></el-button>
            </el-input>
          </el-form-item>
        </el-form>
        <el-button size="medium" type="primary" @click="downExcel"></el-button>
      </div>
      <br/>
      <section>
        <el-table
          ref="multipleTable"
          :data="tableData"
          border
          tooltip-effect="dark"
          style="width: 100%"
          highlight-current-row
          @selection-change="handleSelectionChange"
        >
          <!--          <el-table-column type="selection" width="55"></el-table-column>-->
          <el-table-column label="" type="index" width="60" :index="indexMethod"></el-table-column>
          <el-table-column prop="customer_name" label="" width="160"></el-table-column>
          <el-table-column prop="product_name" label=""></el-table-column>
          <el-table-column prop="phone" label="" width="160"></el-table-column>
          <el-table-column prop="num" label="" width="120"></el-table-column>
          <el-table-column prop="order_set_time" label="" width="260"></el-table-column>
          <el-table-column prop="order_end_time" label="" width="260"></el-table-column>
          <el-table-column prop="cn_order_status" label="" width="120"></el-table-column>
        </el-table>
        <pagination
          :totalNum="total"

          :currentPage="params.pageNum"
          :pageSize="params.pageSize"
          @handleCurrentChange="handleCurrentChange"
        ></pagination>
      </section>
    </div>
  </div>
</template>
<script>
  import pagination from "@/components/pagination/pagination.vue";
  import { toExcel } from "@/utils/excel.js";

  export default {
    components: {
      pagination
    },
    data() {
      return {
        params: {
          pageNum: 1,
          pageSize: 10,
          keyWord: '',
        },
        lazyParams: {
          keyWord: '',
        },
        tableData: [],
        total: 0,
        selectOrderList: [], // 
        excelDialogVisible: false
      };
    },
    methods: {
      handleSelectionChange(value) {
        this.selectOrderList = value;
      },
      indexMethod(index) {
        const num = this.params.pageNum;
        return (num - 1) * this.params.pageSize + index + 1;
      },
      handleCurrentChange(value) {
        this.params.pageNum = value;
        this.loadTableData();
      },
      searchKeyWord() {
        this.params.pageNum = 1;
        this.params.keyWord = this.lazyParams.keyWord;
        this.loadTableData();
      },
      listFormatPipe(list) {
        list.forEach(item => {
          item.order_set_time = item.order_set_time && new Date(item.order_set_time).toLocaleString();
          item.order_end_time = item.order_end_time && new Date(item.order_end_time).toLocaleString();

          //  - 0 1
          item.cn_order_status = item.order_status === 0 ? '' : '';
        });
      },
      loadTableData() {
        this.http.get(this.api.order.list, this.params).then(res => {
          if (res.status !== 200) {
            this.tableData = [];
            this.total = 0;
            this.$message.error(res.message);
            return;
          }
          this.listFormatPipe(res.data.list);
          this.tableData = res.data.list;
          this.total = res.data.total;
        });
      },
      async downExcel() {
        const params = { ...this.params };
        delete params.pageNum;
        delete params.pageSize;

        const res = await this.http.get(this.api.order.export, params);
        if (res.status !== 200) {
          this.$message.error(res.message);
          return;
        }

        const data = res.data;
        this.listFormatPipe(data);

        data.forEach((item, index) => {
          item.index = index + 1;
        });

        const th = [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          ""
        ];
        const filterVal = [
          "index",
          "customer_name",
          "product_name",
          "phone",
          "num",
          "order_set_time",
          "order_end_time",
          "cn_order_status"
        ];

        const excelData = data.map(v => filterVal.map(k => v[k]));
        toExcel({
          th,
          data: excelData,
          fileName: '',
          fileType: 'xlsx',
          sheetName: '',
        });
      },
    },
    created() {
      this.loadTableData();
    },
  };
</script>
<style lang="scss" scoped>
  .data-utils {
    display: flex;
    justify-content: space-between;
  }

  .search-form {

    /deep/ {
      .el-form-item {
        margin-bottom: 0;
      }
    }
  }

  .content-wrapper {
    margin-top: 0;
  }
</style>




